.breadcrumbs {
  margin: 0 0 40px 0;
  &__items {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }
  &__item {
    font-size: 14px;
    line-height: 120%;
    color: #636363;
    margin-top: 20px;
    position: relative;
    &:not(:last-child) {
      margin-right: 25px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -14px;
        transform: translate(0, -50%);
        width: 3px;
        height: 5px;
        background-image: url("../img/arrow.svg");
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
      }
    }
    &--active {
      color: #000;
    }
    &:hover {
      color: #000;
    }
  }
}
.btn-green {
  background: #95DB35;
  border-radius: 4px;
  font-weight: 900;
  font-size: 15px;
  line-height: 120%;
  border: 1px solid #95db35;
  color: #fff;
  &:hover {
    background: #fff;
    border: 1px solid #95db35;
    color: #95db35;
  }
}
.display2 {
  font-weight: 700;
  font-size: 36px;
  line-height: 100%;
  color: #000000;
  text-transform: uppercase;
  span {
    background: linear-gradient(90deg, #1D98B0 -26.92%, rgba(255, 255, 255, 0) 118.27%);
    border-radius: 90px;
  }
}
.equipment__card {
  height: 100%;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #FFFFFF;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);
  border-radius: 4px;
  &:hover {
    box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.2);
  }
  &-left {
    width: calc(100% - 275px);
    display: flex;
    justify-content: center;
    img {
      //width: 100%;
      max-width: 100%;
      max-height: 265px;
    }
  }
  &-right {
    width: 245px;
    margin-left: 30px;
  }
  &-title {
    font-size: 16px;
    line-height: 120%;
    color: #000000;
  }
  &-list {
    margin-top: 28px;
    max-width: 195px;
    li {
      font-size: 11px;
      line-height: 100%;
      color: #636363;
      padding-left: 10px;
      position: relative;
      &:not(:first-child) {
        margin-top: 12px;
      }
      &:after {
        content: '';
        position: absolute;
        top: 4px;
        left: 0;
        width: 3px;
        height: 3px;
        background: #005492;
        border-radius: 50%;
      }
    }
  }
  &-link {
    display: inline-block;
    margin-top: 28px;
    font-weight: 900;
    font-size: 15px;
    line-height: 120%;
    color: #95DB35;
    position: relative;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -1px;
      width: 100%;
      height: 1px;
      background: #95db35;
    }
  }
}
.form {
  &-group {
    margin: 0;
    padding: 0;
    border: none;
    input {
      font-size: 14px;
      line-height: 100%;
      color: #898989;
      background: #EFEFEF;
      border-radius: 4px;
      border: none;
      padding: 0 15px;
    }
    textarea {
      font-size: 14px;
      line-height: 100%;
      color: #898989;
      background: #EFEFEF;
      border-radius: 4px;
      border: none;
      resize: none;
      padding: 15px;
    }
  }
}
.swiper-button{
  width: 42px;
  height: 42px;
  flex-shrink: 0;
  margin: 0;
  background: #95db35;
  &:after{
    display: none;
  }
  &-prev{
    margin-right: 7px;
  }
}

@media screen and (max-width: 1150px) {
  .equipment__card {
    &-left {
      width: calc(100% - 225px);
    }
    &-right {
      width: 210px;
      margin-left: 15px;
    }
  }
}

@media screen and (max-width: 991px) {
  .breadcrumbs {
    margin: 13px 0 20px;
    &__item {
      margin-top: 7px;
    }
  }
  .display2 {
    font-size: 30px;
  }
  .equipment__card {
    flex-direction: column;
    padding: 30px 15px;
    &-left {
      width: 100%;
      max-width: 320px;
      margin: 0 auto;
    }
    &-right {
      width: 100%;
      margin: 10px 0 0 0;
      text-align: center;
    }
    &-title {
      font-size: 16px;
    }
    &-link {
      font-size: 15px;
      margin-top: 30px;
    }
    &-list {
      display: none;
    }
  }
}

@media screen and (max-width: 767px) {
  .display2 {
    font-size: 25px;
  }
}

@media screen and (max-width: 576px) {
  .display2 {
    font-size: 22px;
  }
}