@media screen and (min-width:993px) {
  .wrapper {
    padding-left: 78px;
  }
}
.sidebar {
  position: fixed;
  z-index: 4;
  top: 0;
  left: 0;
  width: 78px;
  height: 100vh;
  background: #002B49;
  &__basket {
    width: 100%;
    height: 90px;
    background: #007BD3;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    span {
      position: absolute;
      left: 50%;
      bottom: -20px;
      transform: translate(-50%, 0);
      width: 45px;
      height: 45px;
      background: #46B1FF;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 700;
      font-size: 20px;
      line-height: 120%;
      color: #fff;
    }
  }
  &__search {
    width: 56px;
    height: 56px;
    background: #003760;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 60px auto 0;
  }
  &__languages {
    background: #003760;
    border-radius: 90px;
    margin: 6px auto 0;
    width: 56px;
    min-height: 118px;
    display: flex;
    justify-content: center;
    align-items: center;
    writing-mode: vertical-rl;
    transform: rotate(180deg);
  }
  &__language {
    font-weight: 900;
    font-size: 15px;
    line-height: 120%;
    color: #005595;
    &:not(:first-child) {
      margin-top: 12px;
    }
    &--active {
      color: #fff;
    }
    &:hover {
      color: #fff;
    }
  }
  &__img {
    margin: 25px auto 35px;
    &-mob {
      display: none;
    }
  }
  &__btn {
    height: 266px;
    width: 56px;
    margin: 0 auto;
    span {
      writing-mode: vertical-rl;
      transform: rotate(180deg);
    }
  }
}
.main {
  padding: 175px 0 0;
  color: #fff;
  background-image: url("../img/main-bg.webp");
  background-position: center;
  background-size: cover;
  position: relative;
  z-index: 3;
  min-height: 800px;
  overflow: hidden;
  &__img-bg {
    position: absolute;
    left: 0;
    bottom: -620px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 2;
  }
  &__left {
    width: 420px;
    margin-right: 30px;
    padding-top: 50px;
  }
  &__right {
    width: calc(100% - 450px);
  }
  &__subtitle {
    font-weight: 900;
    font-size: 24px;
    line-height: 120%;
  }
  &__title {
    font-weight: 900;
    font-size: 50px;
    line-height: 100%;
    margin-top: 18px;
  }
  &__info {
    font-size: 24px;
    line-height: 120%;
    margin-top: 18px;
  }
  &__img {
    margin-right: -100px;
    img {
      width: 100%;
    }
  }
}
.equipment {
  padding: 10px 0 40px 0;
  position: relative;
  z-index: 3;
  overflow: hidden;
  background-image: url("../img/bg-block3.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__img-bg {
    position: absolute;
    top: 200px;
    right: -50px;
    pointer-events: none;
  }
  &__img-bg2 {
    position: absolute;
    left: -250px;
    bottom: 550px;
    transform: rotate(180deg);
    pointer-events: none;
  }
  &__img-bg3 {
    position: absolute;
    z-index: -1;
    left: 0;
    bottom: -28%;
    width: 100%;
    height: 1000px;
  }
  &__title {
    text-transform: uppercase;
  }
  &__cards {
    margin-top: 50px;
    &-bottom {
      margin-top: 40px;
    }
    &-title {
      font-weight: 900;
      font-size: 26px;
      line-height: 120%;
      color: #000000;
      margin-bottom: 25px;
    }
    &-top {
      margin-top: 25px;
      position: relative;
    }
  }
  &__btn {
    max-width: 280px;
    height: 64px;
    padding: 0 15px;
    margin: 40px auto 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .swiper-btns {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .swiper-button-prev {
    position: static;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #95DB35;
    border-radius: 4px;
    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    position: static;
    width: 40px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #95DB35;
    border-radius: 4px;
    margin-left: 15px;
    &:after {
      display: none;
    }
  }
  .swiper-slide {
    width: 100% !important;
  }
  @media screen and (min-width: 576px) {
    .swiper {
      overflow: unset;
    }
    .swiper-wrapper {
      display: grid;
      grid-template-columns: repeat(2,calc((100% / 2) - (22px * 1 / 2)));
      gap: 30px;
      transform: unset !important;
    }
    .swiper-button-next,
    .swiper-button-prev {
      display: none;
    }
  }
  &__img {
    position: relative;
    z-index: -2;
    max-width: 1385px;
    margin: 70px auto 0;
    padding: 0 15px;
    img {
      width: 100%;
    }
  }
}
.certificates{
  padding: 42px 0 45px;
  position: relative;
  z-index: 3;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__title{
    font-weight: 900;
    font-size: 26px;
    line-height: 32px;
    position: relative;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 180px;
      height: 3px;
      background-color:  #E2E2E2;
      flex-shrink: 0;
      margin: 0 30px;
    }
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper{
    &-container{
      margin-top: 28px;
      min-height: 410px;
    }
    &-button{
      position: static;
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      min-height: 223px;
      width: 162px;
      height: auto;
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
      display: flex;
      flex-direction: column;
      justify-content: center;
      &-next,
      &-next+.swiper-slide+.swiper-slide{
        width: 215px;
        .certificate-card{
          &__img{
            padding: 14px;
          }
        }
      }
      &-next+.swiper-slide{
        width: 275px;
        .certificate-card{
          &__img{
            padding: 20px;
          }
        }
      }
      &-active,
      &-next,
      &-next+.swiper-slide,
      &-next+.swiper-slide+.swiper-slide,
      &-next+.swiper-slide+.swiper-slide+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}
.certificate-card{
  &__img{
    background: #E8F2F1;
    box-shadow: 0px 20px 10px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    padding: 12px;
    display: block;
    &-wrap{
      width: 100%;
      padding-bottom: 157%;
      position: relative;
      display: block;
      img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
.bg-block {
  background-image: url("../img/bg-block.webp");
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: cover;
  position: relative;
  overflow: hidden;
  &__img {
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1200px;
    pointer-events: none;
  }
  &__img2 {
    position: absolute;
    z-index: -1;
    top: 50px;
    left: 0;
    pointer-events: none;
  }
  &__img3 {
    position: absolute;
    z-index: -1;
    top: 50px;
    right: 0;
    pointer-events: none;
  }
}
.about{
  padding-bottom: 42px;
  position: relative;
  z-index: 3;
  overflow: hidden;
  &__title,
  &__inner{
    position: relative;
    z-index: 2;
  }
  &__title{
    text-transform: uppercase;
  }
  &__inner{
    padding: 45px 50px 48px;
    background-image: url(../img/about-bg.webp);
    background-size: cover;
    background-position: center;
    margin-top: 31px;
    color: #FFFFFF;
  }
  &__desc1,
  &__desc2{
    font-size: 18px;
    line-height: 120%;
  }
  &__desc1{
    font-weight: 700;
    margin-bottom: 15px;
  }
  &__nav{
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
  }
  &__btn{
    padding: 0 35px;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.exhibition{
  padding: 45px 0 90px;
  position: relative;
  z-index: 3;
  overflow: hidden;
  &__grid{
    margin-top: 60px;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 30px;
  }
  &__nav{
    margin-top: 30px;
    display: flex;
    justify-content: center;
  }
  &__btn{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 56px;
    padding: 0 36px;
  }
}
.exhibition-card{
  position: relative;
  display: block;
  &__img{
    padding-bottom: 61%;
    position: relative;
    filter: drop-shadow(3px 4px 20px rgba(0, 0, 0, 0.1));
    border-radius: 4px;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: calc(100% - 53px);
      background: linear-gradient(360deg, #398697 0%, rgba(255, 255, 255, 0) 100%);
      border-radius: 4px;
      z-index: 2;
    }
    img{
      border-radius: inherit;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 30px;
    z-index: 2;
    color: #FFFFFF;
  }
  &__date{
    font-size: 14px;
    line-height: 120%;
  }
  &__title{
    margin-top: 9px;
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    min-height: 44px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
}
.reviews {
  padding: 90px 0 45px;
  position: relative;
  z-index: 3;
  overflow: hidden;
  &__inner {
    position: relative;
  }
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__title {
    position: relative;
    padding-right: 25px;
    &:after {
      content: '';
      position: absolute;
      z-index: -1;
      top: 50%;
      left: 100%;
      transform: translate(0,-50%);
      width: 730px;
      height: 3px;
      background: #E2E2E2;
    }
  }
  &__btn {
    width: 239px;
    height: 56px;
    position: relative;
    z-index: 2;
    &:after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0);
      width: calc(100% + 50px);
      height: 100%;
      background: transparent;
    }
  }
  &__slider {
    position: relative;
    margin-top: 60px;
  }
  .swiper-slide {
    text-align: center;
  }
  &__slide {
    &-img {
      background: #E8F2F1;
      box-shadow: 0px 20px 10px rgba(0, 0, 0, 0.2);
      border-radius: 4px;
      padding: 15px;
      display: block;
      img {
        width: 100%;
      }
    }
    &-desc {
      font-size: 18px;
      line-height: 100%;
      color: #000000;
      margin-top: 35px;
    }
  }
  &__nav {
    justify-content: flex-end;
    display: none;
    .swiper-button-prev {
      position: static;
    }
    .swiper-button-next {
      position: static;
    }
  }
}
.contacts {
  color: #fff;
  position: relative;
  height: 535px;
  .map {
    height: 535px;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__inner {
    position: relative;
    z-index: 2;
    max-width: 375px;
    height: 100%;
    margin: 0 0 0 auto;
  }
  &__content {
    background-image: url("../img/contacts-bg.webp");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    max-width: 470px;
    padding: 40px 40px 80px;
    margin: 75px 0 0 auto;
  }
  &__title {
    color: #fff;
  }
  &__socials {
    margin-top: 50px;
  }
  &__social {
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: 27px;
    }
    &-icon {
      width: 25px;
      img {
        width: 100%;
      }
    }
    &-desc {
      margin-left: 18px;
    }
    &-title {
      font-size: 14px;
      line-height: 120%;
    }
    &-text {
      font-weight: 700;
      font-size: 18px;
      line-height: 120%;
    }
  }
}


@media screen and (max-width: 1600px) {
  .main {
    &__img {
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 1300px){
  .certificates{
    .swiper{
      &-container{
        min-height: 690px;
        margin-left: auto;
        margin-right: auto;
      }
      &-slide{
        &-next{
          width: 38%;
          &+.swiper-slide~.swiper-slide{
            opacity: 0 !important;
            pointer-events: none !important;
          }
        }
        &-active,
        &-next+.swiper-slide{
          width: calc(31% - 30px);
        }
      }
    }
  }
}

