      gap: unset;
    }
    .swiper-slide {
      height: auto;
      opacity: 0;
      transition: all .3s;
      &-active,
      &-next,
      &-next + .swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
    .swiper-btns {
      margin-top: 50px;
    }
    &__img {
      margin-top: 20px;
    }
  }
  .certificates{
    padding: 25px 0;
    &__title{
      font-size: 18px;
      line-height: 22px;
      &:after{
        margin: 0 20px;
      }
    }
    .swiper{
      &-container{
        width: calc(100% - 90px);
      }
      &-slide{
        width: 100%;
        opacity: 0 !important;
        pointer-events: none !important;
        &-active{
          width: 100% !important;
          opacity: 1 !important;
          pointer-events: unset !important;
        }
      }
    }
  }
  .about{
    &__inner{
      padding: 35px 28px 25px;
    }
    &__desc1 {
      font-size: 16px;
    }
  }
  .exhibition{
    padding: 45px 0;
    &__grid{
      margin-top: 27px;
      gap: 10px;
      grid-template-columns: 100%;
      .exhibition-card:nth-child(1) ~ .exhibition-card{
        display: none;
      }
    }
    &__btn{
      width: calc(100% - 60px);
      padding: 0;
    }
  }
  .reviews {
    padding: 45px 0 130px;
    &__title {
      &:after {

      }
    }
    &__btn {
      position: absolute;
      left: 50%;
      bottom: -86px;
      transform: translate(-50%,0);
    }
    &__nav {
      display: flex;
      position: relative;
      &:before {
        content: '';
        position: absolute;
        z-index: 1;
        top: 0;
        left: 50%;
        width: calc(100% + 50px);
        transform: translate(-50%, 0);
        height: 100%;
        background: #fff;
      }
    }
    &__slider {
      margin-top: 30px;
    }
    &__slide {
      &-desc {
        font-size: 16px;
      }
    }
  }
  .contacts {
    height: 365px;
    .map {
      height: 365px;
    }
    &__inner {
      height: 365px;
    }
    &__content {
      bottom: -250px;
    }
  }
}

@media screen and (max-width: 399px) {
  .sidebar {
   display: none;
 }
  .equipment {
    &__cards-bottom {
      margin-top: 15px;
    }
  }
  .certificates{
    padding: 32px 0 23px;
    &__inner{
      position: relative;
      padding-bottom: 90px;
    }
    &__top{
      display: block;
    }
    &__title{
      text-align: center;
      display: block;
      &:after{
        display: none;
      }
    }
    &__nav{
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
    }
  }
  .about{
    &__inner{
      padding: 37px 30px 30px;
      margin-top: 35px;
    }
    &__btn{
      width: 100%;
    }
  }
  .contacts {
    &__content {
      padding: 15px;
    }
  }
}